	module.exports = {"data":{"post":{"html":"<p>We spent New Year's at home with Christopher. We prepared a simple raclett (gouda cheese, not the real stuff) and fascinated Christopher with a <a href=\"http://www.britannia.org/recipes/showrecipe.php?RecipeID=00000104\">Feuerzangenbowle</a> (punch laced with flaming rum-soaked sugar). For the first time ever we bought fireworks that were going to show him, but he pooped out about 10 pm, so we had to wait impatiently until midnight to shoot them off without him. Today we moped around the house, reading (Mama a mystery, Christopher his new airplane book, me a tech book on <a href=\"http://xml.apache.org/cocoon/\">Cocoon</a>).</p>\n<p>Here's wishing everyone a happy and healthy 2003!</p>","fields":{"title":"Quiet Day","date":"01 Jan 2003","url":"https://papascott.de/2003-01-01-quiet-day/"}}},"pathContext":{"slug":"/2003-01-01-quiet-day/","prev":{"fields":{"title":"Million Dollar Authoring App","date":"31 Dec 2002","slug":"/2002-12-31-million-dollar-authoring-app/"}},"next":{"fields":{"title":"Five Dollar Bookmarklet","date":"02 Jan 2003","slug":"/2003-01-02-five-dollar-bookmarklet/"}}}}

